"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import Link from "next/link";
import { motion } from "framer-motion";

import { useToast } from "@/shared/ui/use-toast";
import { registerSchema, RegisterFormValues } from "../model";
import { useAuthContext } from "@/app/providers/auth-provider";
import { SpaceInput } from "@/shared/ui/space-input";
import { SpaceButton } from "@/shared/ui/space-button";
import { SpaceProgressLoader } from "@/shared/ui/space-progress-loader";

export function RegisterForm() {
  const router = useRouter();
  const { toast } = useToast();
  const { register: registerUser, isLoading } = useAuthContext();
  const [showPassword, setShowPassword] = useState(false);
  const [serverError, setServerError] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [regProgress, setRegProgress] = useState(0);

  const form = useForm<RegisterFormValues>({
    resolver: zodResolver(registerSchema),
    defaultValues: {
      name: "",
      email: "",
      password: "",
      passwordConfirm: "",
    },
  });

  async function onSubmit(data: RegisterFormValues) {
    setServerError(null);
    setIsSubmitting(true);
    setRegProgress(0);

    try {
      // Имитация процесса регистрации с несколькими этапами
      // Этап 1: Валидация данных
      await simulateProgress(0, 20);

      // Этап 2: Проверка доступности email
      await simulateProgress(20, 40);

      // Этап 3: Создание учетной записи
      await simulateProgress(40, 65);

      // Этап 4: Настройка профиля
      await simulateProgress(65, 85);

      // Этап 5: Завершение регистрации
      const result = await registerUser(data);

      if (result.user) {
        // Завершение загрузки
        await simulateProgress(85, 100);

        toast({
          title: "Регистрация выполнена успешно",
          description: "Вы будете перенаправлены в личный кабинет",
        });

        // Задержка перед перенаправлением
        setTimeout(() => {
          router.push("/dashboard");
        }, 1000);
      }
    } catch (error) {
      const errorMessage =
        error instanceof Error
          ? error.message
          : "Произошла ошибка при регистрации";

      setServerError(errorMessage);
      toast({
        variant: "destructive",
        title: "Ошибка регистрации",
        description: errorMessage,
      });
      setIsSubmitting(false);
    }
  }

  // Вспомогательная функция для имитации прогресса
  const simulateProgress = async (start: number, end: number) => {
    const duration = 800; // 0.8 секунды на этап
    const steps = 8;
    const increment = (end - start) / steps;

    for (let i = 0; i <= steps; i++) {
      setRegProgress(start + increment * i);
      await new Promise((resolve) => setTimeout(resolve, duration / steps));
    }
  };

  const formFields = [
    {
      name: "name" as const,
      label: "ФИО",
      placeholder: "Иванов Иван Иванович",
      type: "text",
    },
    {
      name: "email" as const,
      label: "Email",
      placeholder: "email@example.com",
      type: "email",
    },
    {
      name: "password" as const,
      label: "Пароль",
      placeholder: "••••••••",
      type: "password",
      showPasswordToggle: true,
    },
    {
      name: "passwordConfirm" as const,
      label: "Подтверждение пароля",
      placeholder: "••••••••",
      type: "password",
      showPasswordToggle: true,
    },
  ];

  // Анимация для формы
  const formAnimation = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        staggerChildren: 0.1,
        delayChildren: 0.2,
      },
    },
  };

  const itemAnimation = {
    hidden: { opacity: 0, y: 10 },
    visible: { opacity: 1, y: 0 },
  };

  return (
    <>
      <SpaceProgressLoader
        isLoading={isSubmitting}
        progress={regProgress}
        message="Подготовка к регистрации..."
      />

      <motion.form
        onSubmit={form.handleSubmit(onSubmit)}
        className="space-y-5"
        variants={formAnimation}
        initial="hidden"
        animate="visible"
      >
        {formFields.map((field) => (
          <motion.div key={field.name} variants={itemAnimation}>
            <SpaceInput
              label={field.label}
              type={field.type}
              placeholder={field.placeholder}
              showPasswordToggle={field.showPasswordToggle}
              {...form.register(field.name)}
              error={form.formState.errors[field.name]?.message as string}
            />
          </motion.div>
        ))}

        <motion.div className="flex items-center mt-4" variants={itemAnimation}>
          <input
            id="terms"
            name="terms"
            type="checkbox"
            className="h-4 w-4 rounded border-gray-700 bg-gray-900 text-blue-600 focus:ring-blue-500"
          />
          <label htmlFor="terms" className="ml-2 block text-sm text-gray-300">
            Я согласен с{" "}
            <Link
              href="#"
              className="text-blue-400 hover:text-blue-300 transition-colors"
            >
              условиями использования
            </Link>{" "}
            и{" "}
            <Link
              href="#"
              className="text-blue-400 hover:text-blue-300 transition-colors"
            >
              политикой конфиденциальности
            </Link>
          </label>
        </motion.div>

        {serverError && (
          <motion.div
            className="bg-red-500/10 border border-red-500/30 rounded-md p-3 text-sm text-red-200"
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3 }}
          >
            {serverError}
          </motion.div>
        )}

        <motion.div variants={itemAnimation}>
          <SpaceButton
            type="submit"
            className="w-full"
            isLoading={isLoading || isSubmitting}
          >
            Зарегистрироваться
          </SpaceButton>
        </motion.div>
      </motion.form>
    </>
  );
}
